.hotel {
  padding: 23px 0 70px 0;
  &-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    &__offer {
      margin-left: 14px;
      margin-top: 4px; }
    &__name {
      margin-top: 4px;
      margin-bottom: 0; }
    &__description {
      margin: 5px 0; } }
  &-name {
    font-size: 24px;
    line-height: 30px; }
  &-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  &-description {
    font-size: 12px;
    line-height: 15px; }
  &-grid {
    display: flex;
    justify-content: space-between; }
  &-right {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  &-slider {
    width: 728px;
    height: 450px;
    &__item {
      width: 100%;
      height: 100%;
      position: relative;
      border-radius: 8px;
      overflow: hidden;
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 2;
        background: linear-gradient(180deg, rgba(0, 122, 223, 0.48) 0%, rgba(255, 255, 255, 0.128) 100%); } }
    &__button {
      cursor: pointer;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 5;
      width: 30px;
      height: 30px;
      border: none;
      background-size: 10px;
      background-color: transparent;
      &_prev {
        left: 18px;
        background: url('../img/slider/arrow-prev.svg') no-repeat center; }
      &_next {
        right: 18px;
        background: url('../img/slider/arrow-next.svg') no-repeat center; } } } }
.stars {
  flex-basis: 100%; }
.offer {
  font-size: 10px;
  line-height: 13px;
  padding: 4px 9px;
  background-color: $primary;
  border-radius: 3px;
  color: $light; }
.rating {
  max-width: 65px;
  min-height: 45px;
  color: $light;
  text-align: center;
  background-color: $primary;
  border-radius: 4px;
  &__text {
    font-size: 8px;
    line-height: 10px; }
  &__counter {
    font-size: 14px;
    line-height: 18px; } }
.booking {
  background-color: $dark;
  color: $light;
  width: 350px;
  min-height: 210px;
  border-radius: 8px;
  padding: 28px;
  &__info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 9px; }
  &__price {
    display: flex;
    flex-direction: column; }
  &__start {
    font-weight: normal;
    font-size: 10px;
    line-height: 14px; }
  &__pricetag {
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 33px; }
  &__per-room {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px; }
  &__room {
    margin-top: 4px; }
  &__text {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 8px; }
  &__description {
    margin-left: 9px; }
  &__call-center {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 17px; }
  &__heading {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px; }
  &__number {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: inherit;
    text-decoration: none;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px; }
  &__num {
    margin-left: 8px; }
  &__button {
    width: 100%;
    min-height: 45px;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    border-radius: 4px;
    background-color: $primary;
    color: $light;
    border: none;
    cursor: pointer; } }
.map {
  width: 347px;
  height: 213px;
  background-color: #000000;
  border-radius: 10px;
  overflow: hidden; }
