@media (max-width: 1200px) {
  .container {
    max-width: 960px; }
  .hotel {
    &-slider {
      width: 590px;

      &__image {
        width: 100%;
        height: 100%;
        object-fit: cover; } } }
  .card {
    width: 230px;
    height: 230px; } }

@media (max-width: 992px) {
  .container, .newsletter-wrapper {
    max-width: 700px; }
  .logo {
    width: 132px; }
  .navbar {
    &-top {
      padding-top: 30px;
      padding-bottom: 30px; }

    &__search {
      margin-left: auto; }

    &-menu {
      padding-top: 22px;
      padding-bottom: 22px;

      &__link {
        font-size: 12px;
        line-height: 15px; } } }
  .search {
    &__input {
      width: 340px; } }
  .hotel {
    &-grid {
      flex-direction: column; }

    &__slider {
      margin-bottom: 30px; }

    &-right {
      flex-direction: row; }

    &-slider {
      width: 100%; } }
  .booking {
    width: 340px; }
  .map {
    width: 340px; }
  .reviews {
    &-slider {
      &__item {
        max-width: 580px; } } }
  .activities {
    &-wrapper {
      flex-wrap: wrap; }

    &__card {
      margin-bottom: 20px; } }
  .card {
    width: 340px;
    height: 255px; } }

@media (max-width: 768px) {
  .container {
    max-width: 576px;
    width: 92%; }
  .navbar {
    &__search, &-menu, &__user {
      display: none; } }
  .menu {
    &-button {
      width: 16px;
      height: 11px;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: column;
      background: $light;
      border: none;
      cursor: pointer;

      &__line {
        width: 16px;
        height: 1px;
        background-color: $text-color; } } }
  .stars {
    flex-basis: auto; }
  .hotel {
    &-name {
      flex-basis: 100%; }

    &-wrapper {
      justify-content: space-between; }

    &-info {
      &__text {
        width: 100%; }

      &__name {
        margin-top: 10px;
        margin-bottom: 0; }

      &__rating {
        display: none; }

      &__offer {
        order: -1;
        margin-left: 0; } } }
  .hotel {
    &-right {
      flex-direction: column;
      min-height: 450px; }

    &-slider {
      height: 350px; } }
  .booking {
    width: 100%; }
  .map {
    width: 100%; }
  .newsletter {
    &-wrapper {
      max-width: 576px;
      width: 92%;
      flex-direction: column;
      align-items: flex-start; } }
  .reviews {
    &__title {
      font-size: 30px;
      line-height: 38px; }

    &-slider {
      &__item {
        padding: 0 30px 30px;

        &:after, &:before {
          display: none; } }

      &__button {
        top: 32px;

        &_prev {
          left: 50%;
          transform: translate(-50%, 0);
          margin-left: -65px; }

        &_next {
          margin-right: -65px;
          right: 50%;
          transform: translate(50%, 0); } } } }
  .activities {
    padding-top: 40px;
    padding-bottom: 60px; }
  .card {
    width: 48%; } }

@media (max-width: 576px) {
  .logo {
    width: 99px; }
  .hotel {
    &__slider {
      margin-bottom: 20px;
      height: 250px; } }
  .newsletter {
    padding: 77px 0 83px 0;

    &__title {
      margin-bottom: 37px; } }
  .subscribe {
    width: 100%; }
  .reviews {
    &__title {
      font-size: 18px;
      line-height: 23px; } }
  .card {
    width: 100%; } }

@media (max-width: 320px) {
  .hotel {
    &__slider {
      height: 180px; } }
  .reviews {
    &__title {
      max-width: 245px;
      margin: 0 auto 42px; }

    &-slider {
      &__text {
        text-align: left; } } } }
